.home {
  min-height: 100vh;
  background-image: url("../../assets/wood.svg");
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  position: relative;

  &__logo {
    width: 17rem;
    margin: 0 6rem;
  }

  &__header {
    height: 9.6rem;
    background-color: #063892;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__topItems {
    margin-top: 8.3rem;
    position: relative;
  }

  &__logoAnticorrupcao {
    width: 30rem;
    margin: 0 6rem;
  }

  &__portaRetrato {
    width: 27rem;
    position: absolute;
    left: 12rem;
    opacity: 0;
    top: 10rem;
    cursor: pointer;
  }

  &__monitor {
    width: 92rem;
    position: absolute;
    left: 53rem;
    z-index: 2;
    cursor: pointer;
  }

  &__fone {
    width: 57rem;
    position: absolute;
    left: 118rem;
    top: 2rem;
    z-index: 1;
    opacity: 0;
    cursor: pointer;
  }

  &__tel {
    position: absolute;
    left: 162rem;
    width: 21.8rem;
    top: 10rem;
    opacity: 0;
    cursor: pointer;
  }

  &__pencils {
    position: absolute;
    width: 39.5rem;
    left: 4.8rem;
    top: 34rem;
    opacity: 0;
    cursor: pointer;
  }

  &__paper01 {
    position: absolute;
    width: 48rem;
    left: 33.4rem;
    top: 36rem;
    opacity: 0;
    cursor: pointer;
  }

  &__board {
    position: absolute;
    width: 48.3rem;
    left: 30.6rem;
    top: 36rem;
    opacity: 0;
    cursor: pointer;
  }

  &__key {
    position: absolute;
    width: 56.6rem;
    left: 72.6rem;
    top: 56rem;
    opacity: 0;
    cursor: pointer;
  }

  &__calc {
    position: absolute;
    width: 23.3rem;
    left: 115.3rem;
    top: 32rem;
    opacity: 0;
    cursor: pointer;
  }

  &__mouse {
    position: absolute;
    width: 12.2rem;
    left: 130rem;
    top: 67rem;
    opacity: 0;
    cursor: pointer;
  }

  &__mobile {
    position: absolute;
    width: 14.4rem;
    left: 160rem;
    top: 60rem;
    opacity: 0;
    cursor: pointer;
  }

  &__action {
    margin-top: 87rem;
    height: 10rem;
    opacity: 0.75;
    color: #00386b;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    font-size: 4rem;
    margin: 87rem 4.8rem 0 4.8rem;
    border-radius: 2rem 0;
    position: relative;
    top: 10rem;
    opacity: 0;
  }

  &__mobileBlur {
    height: 20rem;
    width: 20rem;
    background: #063892;
    border-radius: 50%;
    filter: blur(13px);
    left: 157rem;
    position: absolute;
    top: 51rem;
  }
}
